/************************************/
/*** 	 01. Global Variables	  ***/
/************************************/

:root {
	--primary-color: #0f161b;
	--secondary-color: #d4d6E7;
	--text-color: #4a5567;
	--accent-color: #462532;
	--divider-color: #f8f7f7;
	--dark-divider-color: #ffffff30;
	--white-color: #ffffff;
	--error-color: rgb(230, 87, 87);
	--default-font: "Plus Jakarta Sans", sans-serif;
}

/************************************/
/*** 	   02. General css		  ***/
/************************************/

body {
	font-family: var(--default-font);
	font-size: 16px;
	font-weight: 400;
	line-height: 1.6em;
	color: var(--text-color);
	background-color: var(--white-color);
}

p {
	line-height: 1.6em;
	margin-bottom: 1.6em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	font-weight: 600;
	color: var(--primary-color);
	line-height: 1.1em;
	letter-spacing: -0.03em;
}

figure {
	margin: 0;
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
}

a:hover {
	text-decoration: none;
	outline: 0;
}

a:focus {
	text-decoration: none;
	outline: 0;
}

html,
body {
	width: 100%;
	overflow-x: clip;
}

.container {
	max-width: 1300px;
}

.container,
.container-fluid,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
	padding-right: 15px;
	padding-left: 15px;
}

.image-anime {
	position: relative;
	overflow: hidden;
}

.image-anime:after {
	content: "";
	position: absolute;
	width: 200%;
	height: 0%;
	left: 50%;
	top: 50%;
	background-color: rgba(255, 255, 255, 0.3);
	transform: translate(-50%, -50%) rotate(-45deg);
	z-index: 1;
}

.image-anime:hover:after {
	height: 250%;
	transition: all 600ms linear;
	background-color: transparent;
}

.reveal {
	position: relative;
	display: -webkit-inline-box;
	display: -ms-inline-flexbox;
	display: inline-flex;
	visibility: hidden;
	overflow: hidden;
}

.reveal img {
	height: 100%;
	width: 100%;
	-o-object-fit: cover;
	object-fit: cover;
	-webkit-transform-origin: left;
	transform-origin: left;
}

.row {
	margin-right: -15px;
	margin-left: -15px;
}

.row > * {
	padding-right: 15px;
	padding-left: 15px;
}

.row.no-gutters {
	margin-right: 0px;
	margin-left: 0px;
}

.row.no-gutters > * {
	padding-right: 0px;
	padding-left: 0px;
}

.btn-default {
	display: inline-block;
	font-size: 16px;
	font-weight: 700;
	line-height: 1.2em;
	color: var(--white-color);
	background: var(--accent-color);
	padding: 15px 55px 15px 15px;
	border-radius: 8px;
	transition: 0.5s ease-in-out;
	position: relative;
	overflow: hidden;
	z-index: 1;
	border: 1px solid transparent;
}

.btn-default::before {
	position: absolute;
	top: 50%;
	right: 0;
	bottom: 0;
	width: 30px;
	height: 30px;
	border-radius: 8px;
	background-color: var(--white-color);
	//background-image: url(../assets/images/arrow.svg);
	background-repeat: no-repeat;
	background-position: center center;
	transform: translate(-15px, -50%);
	transition: 0.4s ease-in-out;
	font: var(--fa-font-solid);
	display: flex;
	justify-content: center;
	align-items: center;
	content: "\f124";
	color: var(--accent-color);
}

.btn-default::after {
	content: "";
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	width: 0;
	height: 106%;
	border-radius: 8px;
	background: var(--primary-color);
	transition: 0.4s ease-in-out;
	z-index: -1;
}

.btn-default:hover::after {
	width: 106%;
}

.btn-default:hover {
	background-color: transparent;
	border-color: var(--white-color);
}

.btn-default.btn-highlighted {
	margin-left: 20px;
	background-color: var(--white-color);
	color: var(--accent-color);
}

.btn-default.btn-highlighted::before {
	background-color: var(--accent-color);
	//background-image: url(../assets/images/arrow-white.svg);
	content: "\f124";
	color: var(--white-color);
}

.btn-default.btn-highlighted::after {
	background-color: var(--primary-color);
}

.btn-default.btn-highlighted:hover {
	color: var(--white-color);
	background-color: transparent;
}

.btn-large {
	font-size: 20px;
	padding: 20px 65px 20px 20px;
}

.btn-default.btn-large::before {
	width: 40px;
	height: 40px;
	background-size: 15px;
}

#magic-cursor {
	position: absolute;
	width: 10px !important;
	height: 10px !important;
	pointer-events: none;
	z-index: 1000000;
}

#ball {
	position: fixed;
	display: block;
	left: 0;
	top: 0;
	transform: translate(-50%, -50%);
	width: 24px !important;
	height: 24px !important;
	background: transparent;
	border: 1px solid var(--primary-color);
	margin: 0;
	border-radius: 50%;
	pointer-events: none;
	opacity: 1 !important;
}

#ball .circle {
	background: var(--accent-color);
	width: 8px;
	height: 8px;
	top: 50%;
	position: absolute;
	left: 50%;
	transform: translate(-50%, -50%);
	border-radius: 100%;
}

.preloader {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 1000;
	background-color: var(--accent-color);
	display: flex;
	align-items: center;
	justify-content: center;
}

.loading-container,
.loading {
	height: 100px;
	position: relative;
	width: 100px;
	border-radius: 100%;
}

.loading-container {
	margin: 40px auto;
}

.loading {
	border: 1px solid transparent;
	border-color: transparent var(--white-color) transparent var(--white-color);
	animation: rotate-loading 1.5s linear 0s infinite normal;
	transform-origin: 50% 50%;
}

.loading-container:hover .loading,
.loading-container .loading {
	transition: all 0.5s ease-in-out;
}

#loading-icon {
	position: absolute;
	top: 50%;
	left: 50%;
	max-width: 66px;
	transform: translate(-50%, -50%);
}

@keyframes rotate-loading {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}

.light-bg-section {
	position: relative;
	width: 100%;
	max-width: 1600px;
	background: var(--secondary-color);
	border-radius: 40px;
	margin: 0 auto;
	padding: 100px 0;
}

// .light-bg-section::before {
// 	content: "";
// 	position: absolute;
// 	bottom: 0;
// 	left: 30px;
// 	width: 490px;
// 	height: 480px;
// 	background-image: url(../assets/images/icon-section-bg.svg);
// 	background-repeat: no-repeat;
// 	background-position: bottom left;
// }

.light-bg-section .container {
	position: relative;
	z-index: 1;
}

.section-row {
	margin-bottom: 60px;
}

.section-row .section-title {
	margin-bottom: 0;
}

.section-btn {
	text-align: end;
}

.section-btn .btn-default.btn-highlighted {
	margin: 0;
}

.section-title {
	margin-bottom: 30px;
}

.section-title h3 {
	font-size: 13px;
	font-weight: 700;
	text-transform: uppercase;
	color: var(--accent-color);
	margin-bottom: 15px;
	letter-spacing: 0.2em;
}

.section-title h1,
.section-title h2 {
	font-size: 50px;
	letter-spacing: -0.03em;
	font-weight: 600;
	margin-bottom: 0;
}

.section-title h1 span,
.section-title h2 span {
	color: var(--accent-color);
}

.section-title-content p {
	margin: 0;
}

/************************************/
/**** 	   03. Header css		 ****/
/************************************/

header.main-header {
	background-color: var(--white-color);
	position: relative;
	z-index: 100;
}

.navbar {
	padding: 25px 0;
	align-items: center;
}

.navbar-brand {
	padding: 0;
	margin: 0;
}

.main-menu .nav-menu-wrapper {
	flex: 1;
	text-align: center;
}

.main-menu .nav-menu-wrapper > ul {
	align-items: center;
	display: inline-flex;
}

.main-menu ul li {
	margin: 0 5px;
	position: relative;
}

.main-menu ul li a {
	font-size: 16px;
	font-weight: 600;
	padding: 15px 15px !important;
	color: var(--primary-color);
	transition: all 0.3s ease-in-out;
}

.main-menu ul li.submenu > a:after {
	content: "\f107";
	font-family: "FontAwesome";
	font-weight: 900;
	font-size: 14px;
	margin-left: 8px;
}

.main-menu ul li a:hover,
.main-menu ul li a:focus {
	color: var(--accent-color);
}

.main-menu ul ul {
	visibility: hidden;
	opacity: 0;
	transform: scaleY(0.8);
	transform-origin: top;
	padding: 0;
	margin: 0;
	list-style: none;
	width: 210px;
	border-radius: 15px;
	position: absolute;
	left: 0;
	top: 100%;
	overflow: hidden;
	background-color: var(--primary-color);
	transition: all 0.3s ease-in-out;
	text-align: left;
}

.main-menu ul ul ul {
	left: 100%;
	top: 0;
	text-align: left;
}

.main-menu ul ul li {
	margin: 0;
	padding: 0;
}

.main-menu ul ul li a {
	color: var(--white-color);
	padding: 8px 20px !important;
	transition: all 0.3s ease-in-out;
}

.main-menu ul li:hover > ul {
	visibility: visible;
	opacity: 1;
	transform: scaleY(1);
	padding: 5px 0;
}

.main-menu ul ul li a:hover {
	color: var(--accent-color);
	background-color: transparent;
}

.main-menu ul li.highlighted-menu {
	display: none;
}

.responsive-menu,
.navbar-toggle {
	display: none;
}

.responsive-menu {
	top: 0;
	position: relative;
}

.slicknav_btn {
	background: var(--accent-color);
	padding: 6px 0 0;
	width: 38px;
	height: 38px;
	margin: 0;
	border-radius: 6px;
}

.slicknav_icon .slicknav_icon-bar {
	display: block;
	width: 100%;
	height: 3px;
	width: 22px;
	background-color: var(--white-color);
	border-radius: 6px;
	margin: 4px auto !important;
	transition: all 0.1s ease-in-out;
}

.navbar-toggle
	a.slicknav_btn.slicknav_open
	.slicknav_icon
	span.slicknav_icon-bar:nth-child(1) {
	transform: rotate(-45deg) translate(-5px, 5px);
}

.navbar-toggle
	a.slicknav_btn.slicknav_open
	.slicknav_icon
	span.slicknav_icon-bar:nth-child(2) {
	opacity: 0;
}

.navbar-toggle
	a.slicknav_btn.slicknav_open
	.slicknav_icon
	span.slicknav_icon-bar:nth-child(3) {
	transform: rotate(45deg) translate(-5px, -5px);
}

.slicknav_menu {
	position: absolute;
	width: 100%;
	padding: 0;
	background: var(--accent-color);
}

.slicknav_menu ul {
	margin: 5px 0;
}

.slicknav_menu ul ul {
	margin: 0;
}

.slicknav_nav .slicknav_row,
.slicknav_nav li a {
	position: relative;
	font-size: 16px;
	font-weight: 400;

	padding: 10px 20px;
	color: var(--white-color);
	line-height: normal;
	margin: 0;
	border-radius: 0 !important;
	transition: all 0.3s ease-in-out;
}

.slicknav_nav a:hover,
.slicknav_nav .slicknav_row:hover {
	background-color: transparent;
	color: var(--primary-color);
}

.slicknav_menu ul ul li a {
	padding: 10px 20px 10px 30px;
}

.slicknav_arrow {
	font-size: 0 !important;
}

.slicknav_arrow:after {
	content: "\f107";
	font-family: "FontAwesome";
	font-weight: 900;
	font-size: 12px;
	margin-left: 8px;
	color: var(--white-color);
	position: absolute;
	right: 15px;
	top: 15px;
	transition: all 0.3s ease-out;
}

.slicknav_open > a .slicknav_arrow:after {
	transform: rotate(-180deg);
}

/************************************/
/***        04. Hero css	      ***/
/************************************/

.google-rating {
	margin-bottom: 20px;
}

.google-rating ul {
	display: inline-flex;
	align-items: center;
	padding: 0;
	margin: 0;
	list-style: none;
	background-color: var(--white-color);
	border-radius: 40px;
	padding: 6px 20px;
}

.google-rating ul li {
	display: inline-block;
	font-size: 14px;
	font-weight: 600;

	margin-right: 10px;
}

.google-rating ul li:last-child {
	margin-right: 0;
}

.google-rating ul li span {
	color: var(--accent-color);
	margin-left: 5px;
}

.google-rating ul li i {
	font-size: 14px;
	color: var(--accent-color);
}

.hero-content-body {
	margin-bottom: 30px;
}

.hero-image {
	text-align: right;
}

/************************************/
/***       05. About Us css	      ***/
/************************************/

.about-us {
	padding: 100px 0 70px;
}

.about-video-image {
	position: relative;
	margin-bottom: 30px;
}

.about-image img {
	border-radius: 40px;
	aspect-ratio: 1/0.4;
	object-fit: cover;
}

.about-video-btn {
	position: absolute;
	bottom: 30px;
	left: 50%;
	transform: translateX(-50%);
	background-color: var(--accent-color);
	border-radius: 100px;
	display: inline-flex;
	align-items: center;
	padding: 5px 20px 5px 5px;
	transition: all 0.3s ease-in-out;
	border: 1px solid transparent;
}

.about-video-btn:hover {
	background-color: var(--primary-color);
	border-color: var(--white-color);
}

.about-video-btn .icon-box a {
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 16px;
	font-weight: 600;

	color: var(--white-color);
}

.about-video-btn .icon-box i {
	background-color: var(--white-color);
	color: var(--accent-color);
	border-radius: 100%;
	width: 40px;
	height: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 10px;
	transition: all 0.3s ease-in-out;
}

.about-video-btn:hover .icon-box i {
	color: var(--primary-color);
}

.about-company-item {
	height: calc(100% - 30px);
	margin-bottom: 30px;
}

.about-company-item .icon-box {
	position: relative;
	background-color: var(--accent-color);
	width: 60px;
	height: 60px;
	border-radius: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 20px;
	overflow: hidden;
	transition: all 0.3s ease-in-out;
}

.about-company-item .icon-box::before {
	content: "";
	display: block;
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: var(--primary-color);
	top: 0;
	left: 0;
	transform: scale(0);
	transition: all 0.3s ease-in-out;
	border-radius: 50px;
}

.about-company-item:hover .icon-box:before {
	transform: scale(1);
}

.about-company-item .icon-box img {
	position: relative;
	z-index: 1;
}

.about-company-content p {
	font-size: 22px;
	font-weight: 600;
	color: var(--primary-color);
	margin: 0;
}

/************************************/
/***     06. Our Services css     ***/
/************************************/

.our-service .section-title-content {
	margin-left: 110px;
}

.service-item {
	position: relative;
	overflow: hidden;
	text-align: center;
	height: calc(100% - 30px);
	margin-bottom: 30px;
}

.service-item::before {
	content: "";
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	background: linear-gradient(
		180deg,
		transparent 52.34%,
		var(--primary-color) 100%
	);
	border-radius: 40px;
	background-repeat: no-repeat;
	background-position: center center;
	z-index: 1;
}

.service-image {
	overflow: hidden;
	border-radius: 40px;
}

.service-image img {
	border-radius: 40px;
	aspect-ratio: 1 / 1.1;
	object-fit: cover;
	transition: all 0.5s ease-out;
}

.service-item:hover .service-image img {
	transform: scale(1.1);
}

.service-content {
	content: "";
	position: absolute;
	bottom: 20px;
	right: 80px;
	left: 80px;
	z-index: 2;
	transition: all 0.5s ease-in-out;
}

.service-content h3 {
	font-size: 22px;
	color: var(--white-color);

	margin-bottom: 20px;
}

.service-content .service-readmore-btn {
	height: 0;
	overflow: hidden;
	transition: all 0.4s ease-out;
}

.service-item:hover .service-content .service-readmore-btn {
	height: 52px;
}

.service-footer-btn {
	text-align: center;
	margin-top: 30px;
}

/************************************/
/***    07. State Counter css     ***/
/************************************/

.state-counter {
	padding: 100px 0;
}

.state-image-counter-1 {
	margin-bottom: 30px;
}

.state-image-counter-2,
.state-image-counter-1 {
	display: flex;
	align-items: center;
	overflow: hidden;
}

.state-image-counter-1 .counter-box {
	background-color: var(--secondary-color);
	width: 30%;
	height: 212px;
	border-radius: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	padding: 0 10px;
	margin-left: 30px;
}

.counter-content h3 {
	font-size: 34px;
	font-weight: 700;
	margin-bottom: 10px;
}

.counter-content p {
	margin: 0;
	color: var(--primary-color);
}

.state-image-counter-1 .counter-image {
	display: block;
	width: 40%;
	background-color: transparent;
	padding: 0;
	border-radius: 40px;
}

.counter-image img {
	border-radius: 40px;
	width: 100%;
	height: 212px;
	object-fit: cover;
}

.state-image-counter-2 .counter-box {
	background-color: var(--secondary-color);
	width: 40%;
	height: 212px;
	border-radius: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	padding: 0 10px;
	margin-left: 30px;
}

.state-image-counter-2 .counter-image {
	display: block;
	width: 30%;
	background-color: transparent;
	padding: 0;
}

/************************************/
/***    08. Why Choose Us css     ***/
/************************************/

.why-choose-us.light-bg-section {
	padding: 100px 0 70px;
	margin-bottom: 100px;
}

.why-choose-image-box {
	position: relative;
	z-index: 1;
	margin-bottom: 30px;
}

.why-choose-image-box::before {
	content: "";
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	background-color: var(--primary-color);
	opacity: 60%;
	border-radius: 40px;
	background-repeat: no-repeat;
	background-position: center center;
	z-index: 1;
}

.why-choose-image img {
	border-radius: 40px;
	aspect-ratio: 1 / 0.31;
	object-fit: cover;
}

.why-choose-content {
	position: absolute;
	bottom: 40px;
	left: 40px;
	right: 40px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	z-index: 1;
}

.why-choose-title {
	width: 100%;
	max-width: 710px;
}

.why-choose-title h2 {
	font-size: 46px;
	color: var(--white-color);
}

.why-choose-counter {
	width: calc(100% - 710px);
	text-align: right;
}

.why-choose-counter h3 {
	font-size: 50px;
	color: var(--white-color);
}

.why-choose-counter p {
	font-size: 18px;
	color: var(--white-color);
	margin: 0;
}

.why-choose-item {
	position: relative;
	background-color: var(--white-color);
	border-radius: 40px;
	height: calc(100% - 30px);
	margin-bottom: 30px;
	padding: 40px;
	transition: all 0.4s ease-in-out;
	overflow: hidden;
}

.why-choose-item::before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 100%;
	border-radius: 0px;
	background: var(--primary-color);
	transition: all 0.5s ease-in-out;
	height: 100%;
}

.why-choose-item:hover:before {
	top: 0;
}

.why-choose-item .icon-box {
	background-color: var(--accent-color);
	width: 60px;
	height: 60px;
	border-radius: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 20px;
	position: relative;
	z-index: 1;
}

.why-choose-body h3 {
	font-size: 22px;
	margin-bottom: 20px;

